import { connect } from "react-redux";

import { Heading, PrimaryContainer, ErrorContainer } from "../UI/utils";
import { Spinner } from "../UI/spinner";

const KnowMore = (props) => {
    let sectionContent = <Spinner />;

    if (props.errMsg !== "") {
        sectionContent = <ErrorContainer>{props.errMsg}</ErrorContainer>;
    }
    // if blogs is not null then only exec
    else if (props.blogs !== null) {
        sectionContent = (
            <div className="text-xs sm:text-sm w-auto">
                <span className="block text-submain font-arimo pb-4 sm:pb-6">
                    Latest Blogs
                </span>
                <ul className="list-disc list-outside pl-4 text-content font-manrop leading-5 tracking-wider sm:tracking-wide lg:text-base flex flex-col space-y-4 sm:space-y-6">
                    {props.blogs.map((blog, index) => (
                        <li key={index}>
                            <a
                                href={blog.url}
                                rel="noopener noreferrer"
                                target="_blank"
                                className="block sm:pb-2 hover:text-main"
                            >
                                {blog.title}
                            </a>
                            <span className="hidden sm:flex flex-row space-x-5 text-submain font-arimo text-xs">
                                {blog.tags.map((tag) => (
                                    <span key={tag}>{tag}</span>
                                ))}
                            </span>
                        </li>
                    ))}
                </ul>
            </div>
        );
    }

    return (
        <PrimaryContainer>
            <div className="pb-10 sm:pb-12 md:pb-14 lg:pb-20">
                <Heading>Know More.</Heading>
                {sectionContent}
            </div>
        </PrimaryContainer>
    );
};

const mapStateToProps = (state) => {
    return {
        blogs: state.home.blogs,
        errMsg: state.home.errorMsgs.blogsErrMsg,
    };
};

export default connect(mapStateToProps, null)(KnowMore);
