export const Heading = (props) => {
    return (
        <div
            className={
                "font-source-serif text-main text-lg md:text-xl lg:text-2xl text-center lg:text-left pb-6 xs:pb-8 md:pb-10 lg:pb-12 " +
                props.customClasses
            }
        >
            {props.children}
        </div>
    );
};

export const MainContainer = (props) => {
    return (
        <main className="bg-primary text-content relative min-h-full min-w-full">
            {props.children}
        </main>
    );
};

// ---- for primary containers ----
export const PrimaryContainer = (props) => {
    // max top padding is ~ 80px
    return (
        <section className="min-h-full max-w-7xl mx-auto px-8 xs:px-10 sm:px-12 md:px-14 lg:px-16  pt-10 sm:pt-12 md:pt-14 lg:pt-20">
            {props.children}
        </section>
    );
};

// ---- for secondary containers (which have diff bg color) ----
export const SecondaryContainer = (props) => {
    // max padding in y dir is ~ 80px
    return (
        <section className="bg-secondary min-h-full">
            <div className="min-h-full max-w-7xl mx-auto px-8 xs:px-10 sm:px-12 md:px-14 lg:px-16  py-10 sm:py-12 md:py-14 lg:py-20">
                {props.children}
            </div>
        </section>
    );
};

export const ErrorContainer = (props) => {
    return <div className="text-base text-center">{props.children}</div>;
};
