import { Component } from "react";
import { NavLink } from "react-router-dom";

class Error404 extends Component {
    componentDidMount() {
        document.title = "Page Not Found | Dhyey Thumar";
    }

    render() {
        return (
            <section className="min-h-screen max-w-xl mx-auto flex flex-col justify-center text-center space-y-4 ">
                <span className="text-main font-arimo text-8xl lg:text-9xl">
                    404
                </span>
                <span className="text-content font-manrope text-2xl lg:text-3xl pb-12">
                    Page Not Found
                </span>
                <NavLink
                    to="/"
                    className="self-center text-main flex-shrink justify-center px-6 py-3 border-2 border-main rounded-md"
                >
                    Go Home
                </NavLink>
            </section>
        );
    }
}

export default Error404;
